.favorites-page {
  padding: 1rem;
  max-width: 600px;
  margin: 0 auto;
}

.favorites-list {
  list-style: none;
  padding: 0;
}

.favorites-list li {
  padding: 0.5rem;
  margin: 0.5rem 0;
  background: var(--card-bg, #f4f4f4);
  border-radius: 6px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
}
