 /* ========== Animations ========== */

@keyframes fadeInSlide {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ========== Animation Classes ========== */

.page-wrapper {
  animation: fadeInSlide 0.6s ease forwards;
}

.fade-in {
  opacity: 0;
  animation: fadeIn 0.8s ease forwards;
}

.animated-title {
  animation: fadeInUp 0.6s ease-out;
}

/* ========== Button Styling ========== */

button {
  transition: background-color 0.3s ease, transform 0.2s ease;
  background: linear-gradient(135deg, #6e8efb, #a777e3);
  color: #fff;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(110, 142, 251, 0.3);
}

button:hover {
  transform: scale(1.05);
  background: linear-gradient(135deg, #5e7de0, #8d61d3);
}

button:disabled {
  background: #bbb;
  cursor: not-allowed;
  opacity: 0.6;
}

/* ========== Dark Mode Buttons ========== */

.dark-mode button {
  background: linear-gradient(135deg, #3a4fba, #6545a5);
  color: #fff;
}

.dark-mode button:hover {
  background: linear-gradient(135deg, #2e3e9d, #593999);
}

/* ========== Utility Styling (Optional) ========== */

input,
select {
  border: 1px solid #ccc;
  border-radius: 6px;
  padding: 0.5rem 0.75rem;
  font-size: 1rem;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

input:focus,
select:focus {
  border-color: #6e8efb;
  box-shadow: 0 0 0 3px rgba(110, 142, 251, 0.2);
}

/* Dark Mode Inputs */
.dark-mode input,
.dark-mode select {
  background-color: #2e2e2e;
  color: #f0f0f0;
  border-color: #555;
}

.dark-mode input:focus,
.dark-mode select:focus {
  border-color: #6e8efb;
  box-shadow: 0 0 0 3px rgba(110, 142, 251, 0.2);
}
