 /* src/styles/FormStyles.css */

/* ========== Page Container ========== */
.page-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: var(--background, #f4f6f8);
  transition: background-color 0.3s ease;
}

.dark-mode .page-container {
  background: #121212;
}

/* ========== Form Wrapper ========== */
.form-wrapper {
  background: #ffffff;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
  width: 100%;
  max-width: 400px;
  animation: fadeInSlide 0.6s ease forwards;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.form-wrapper h2 {
  margin-bottom: 20px;
  text-align: center;
  font-size: 1.8rem;
  color: #333;
}

.dark-mode .form-wrapper {
  background: #1e1e1e;
  color: #f0f0f0;
  box-shadow: 0 8px 24px rgba(255, 255, 255, 0.03);
}

.dark-mode .form-wrapper h2 {
  color: #ffffff;
}

/* ========== Form Inputs ========== */
.form-wrapper input {
  width: 100%;
  padding: 0.6rem 0.9rem;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.form-wrapper input:focus {
  border-color: #6e8efb;
  box-shadow: 0 0 0 3px rgba(110, 142, 251, 0.2);
  outline: none;
}

.dark-mode .form-wrapper input {
  background-color: #2c2c2c;
  color: #f0f0f0;
  border: 1px solid #555;
}

.dark-mode .form-wrapper input:focus {
  border-color: #6e8efb;
  box-shadow: 0 0 0 3px rgba(110, 142, 251, 0.2);
}

/* ========== Submit Button ========== */
.form-wrapper button {
  width: 100%;
  padding: 0.7rem 1rem;
  background: linear-gradient(135deg, #6e8efb, #a777e3);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  box-shadow: 0 4px 12px rgba(110, 142, 251, 0.3);
}

.form-wrapper button:hover {
  background: linear-gradient(135deg, #5e7de0, #8d61d3);
  transform: scale(1.03);
}

.form-wrapper button:disabled {
  background-color: #bbb;
  cursor: not-allowed;
  opacity: 0.6;
}

.dark-mode .form-wrapper button {
  background: linear-gradient(135deg, #3a4fba, #6545a5);
  color: #fff;
}

.dark-mode .form-wrapper button:hover {
  background: linear-gradient(135deg, #2e3e9d, #593999);
}

/* ========== Notes and Text ========== */
.note {
  margin-top: 10px;
  text-align: center;
  font-size: 0.9rem;
  color: #555;
}

.dark-mode .note {
  color: #ccc;
}

/* ========== Animation ========== */
@keyframes fadeInSlide {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
