 /* Reset and Base Styles */
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f9f9fb;
  color: #333;
  margin: 0;
  padding: 0;
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* Navbar Styles */
.navbar {
  background-color: #3a3f51;
  color: white;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.navbar h1 {
  margin: 0;
  font-size: 1.75rem;
}

nav a {
  color: white;
  margin-left: 20px;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s ease;
}

nav a:hover {
  color: #ffcc70;
}

/* Buttons */
button {
  background-color: #4a90e2;
  color: white;
  padding: 10px 16px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  border-radius: 6px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #357ab8;
}

/* Form Container */
.form-container,
form {
  max-width: 420px;
  margin: 50px auto;
  padding: 24px;
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.08);
}

form h2 {
  text-align: center;
  margin-bottom: 1.5rem;
}

input {
  width: 100%;
  padding: 12px;
  margin: 10px 0;
  border-radius: 6px;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

/* Chart Container */
.chart-container {
  width: 90%;
  max-width: 1100px;
  margin: 40px auto;
  padding: 24px;
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.05);
}

.chart-container h2 {
  text-align: center;
  margin-bottom: 20px;
}

/* Page Layout */
.page-container {
  padding: 2rem;
  max-width: 1200px;
  margin: auto;
  background-color: #ffffff;
  color: #333333;
  border-radius: 12px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.05);
}

.page-header {
  font-size: 2.2rem;
  margin-bottom: 1.5rem;
  font-weight: bold;
  text-align: center;
  color: #2d2d2d;
}

.page-content {
  margin-top: 1rem;
}

.page-section {
  margin-bottom: 2rem;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  nav {
    flex-direction: column;
    align-items: flex-start;
  }

  nav a {
    margin: 8px 0;
  }

  .navbar {
    padding: 1rem;
  }

  .form-container,
  .chart-container {
    width: 95%;
  }
}

/* Dark Mode Support */
body.dark-mode {
  background-color: #1e1e1e;
  color: #e4e4e4;
}

body.dark-mode .navbar {
  background-color: #111;
  color: #eee;
}

body.dark-mode nav a {
  color: #ccc;
}

body.dark-mode nav a:hover {
  color: #ffcc70;
}

body.dark-mode .form-container,
body.dark-mode .chart-container,
body.dark-mode .page-container {
  background-color: #2e2e2e;
  color: #ffffff;
}

body.dark-mode button {
  background-color: #3c9ee7;
  color: #fff;
}

body.dark-mode button:hover {
  background-color: #2574b9;
}
